import React, {useEffect, useState} from "react"
import {Container, Row,Col} from "react-bootstrap";
import "./layout.css"
import SEO from "./seo";
import {TransCtx} from '../hooks/useTrans'
import logo from '../images/yarbamalalogo.png'
import {Link} from "gatsby";
import gif from "../images/comercial-gif-fondo negro.gif";
import {useWindowSize} from "../hooks/useWindowSize";

const Layout = ({ children }) => {
  const [scroll, setScroll] = useState(0);
  const {width} = useWindowSize()
  
  const listenToScroll = () => {
    const winScroll =
      document.body.scrollTop || document.documentElement.scrollTop
    
    const height =
      document.documentElement.scrollHeight -
      document.documentElement.clientHeight
    
    const scrolled = winScroll / height
    
    setScroll(scrolled)
  };
  
  useEffect(() => {
    if (typeof window !== `undefined`) {
      window.addEventListener('scroll', listenToScroll);
    }
    
    return () => window.removeEventListener('scroll', listenToScroll)
    
  },[]);
  
  useEffect(() => {
    console.log('scroll', scroll)
    
  },[scroll]);
  
  return (
    <TransCtx.Provider>
      <SEO title="yerba mala"/>
      <main style={{minHeight: '100vh', width: '100vw'}}>
        <div style={{
          position: 'fixed',
          top: 100,
          left: width > 500 ? 0 : -70,
          transform: 'rotate(270deg)',
          color: 'red',
          zIndex: 3,
          overflow:'hidden'
        }}  >
          <div className="marqueeright">
        <p style={{padding: 0}}>
          site por <a href="https://instagram.com/maximocampo">@maximocampo</a>
        </p>
          </div>
        </div>
        <div style={{
          position: 'fixed',
          top: 0,
          left: 0,
          backgroundImage: `url('${gif}')`,
          width:'100%',
          height:'100%',
          backgroundPosition: 'center',
          filter: `blur(${scroll * 10}px)`
        }}>
        </div>
        <>
          <div style={{position: 'fixed', top: 0, width: '100%', zIndex:1}}>
            <Container>
              <Row>
                <Col xs={{span: 6, offset: 3}} sm={{span: 4, offset: 4}} lg={{span: 2, offset: 5}}>
                  <Link to="/">
                    <img src={logo} alt=""/>
                  </Link>
                </Col>
              </Row>
            </Container>
          </div>
          <div style={{position: 'fixed', bottom: 0, width: '100%', zIndex:1}}>
            <Container>
              <Row>
                <Col style={{textAlign: 'center', fontFamily: 'Menlo'}}>
                  <Link to="/hola"><p>HOLA</p></Link>
                </Col>
                <Col style={{textAlign: 'center', fontFamily: 'Menlo'}}>
                  <Link to="/contacto"><p>CONTACTO</p></Link>
                </Col>
              </Row>
            </Container>
          </div>
        </>
        <div style={{paddingTop: 100, paddingBottom: 60}}>
        {children}
        </div>
      </main>
    </TransCtx.Provider>
  )
};

export default Layout
